function calculate() {
    var carPriceInputElement = document.getElementById("carPrice");
    var carPriceString = carPriceInputElement.value;
    var carPriceNumber = parseInt(carPriceString);
    var carTypeElement = document.getElementById("carTypeSelect");
    var carType = carTypeElement.value;
    var result = calc(carType, carPriceNumber);
    console.log("Afgiften er " + result);
    var outputElement = document.getElementById("resultSpan");
    outputElement.innerHTML = "Afgiften er " + result.toString();
}
function calc(carType, price) {
    switch (carType) {
        case "Personbil":
            {
                if (price <= 200000) {
                    var bilAfgift = price * 0.85;
                    return bilAfgift;
                }
                else {
                    var bilAfgift = (price * 1.50) - 130000;
                    return bilAfgift;
                }
            }
        case "Elbil":
            {
                if (price <= 200000) {
                    var elBilAfgift = (price * 0.85) * 0.20;
                    return elBilAfgift;
                }
                else {
                    var elBilAfgift = ((price * 1.50) - 130000) * 0.20;
                    return elBilAfgift;
                }
            }
        default:
            return -1;
    }
}
var sumButton = document.getElementById("calculateFee");
sumButton.addEventListener("click", calculate);
